.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.sys-empty {
    height: 100%;
    width: 100%;
    padding: 0 !important;
    color: var(--main-text-color-2);
    display: flex;
    flex-direction: column;
}

.sys-empty-icon {
    font-size: var(--font-size-big);
    margin-bottom: 10px;
}

.sys-empty-text {
    font-size: var(--font-size-small);
}

::-webkit-scrollbar {
    /*display: none;*/
}

.app-card {
    width: 100%;
    background: var(--main-bg-glass-color-1);
    backdrop-filter: var(--main-glass-filter);
    border-radius: var(--border-radius-2);
}

.app-card-head {
    width: calc(100% - 32px);
    display: flex;
    padding: 11px 16px;
    align-items: center;
}

.app-card-head-title {
    flex-shrink: 0;
    height: 100%;
    margin-right: 10px;
    font-weight: bold;
    font-size: var(--font-size-small);
    color: var(--primary-color-1);
}

.app-card-head-other {
    flex-shrink: 0;
    height: 100%;
    width: calc(100% - 60px);
    display: flex;
    align-items: center;
}

.app-card-main {
    margin: 0 16px;
    padding-bottom: 16px;
    width: calc(100% - 32px);
}

.sys-popup {
    width: 100%;
}

.sys-popup-head {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 43px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: var(--mid-index);
}

.sys-popup-head-title {
    font-weight: bold;
    font-size: var(--font-size-small);
}

.sys-popup-content {
    position: absolute;
    top: 43px;
    left: 0;
    right: 0;
    bottom: 60px;
    width: calc(100% - 32px);
    overflow-y: auto;
    padding: 11px 16px;
}

.sys-popup-bottom {
    position: fixed;
    bottom: 10px;
    left: 0;
    height: 50px;
    width: calc(100% - 30px);
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30vw;
}

.sys-input {
    width: 100%;
    margin-bottom: 16px;
}

.sys-input-title {
    height: 30px;
    font-weight: bold;
    font-size: var(--font-size-mid);
    margin-bottom: 11px;
}

.sys-input-content {
    height: 50px;
    width: 100%;
}

.sys-input-content input {
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    background: var(--main-bg-color-1);
    color: var(--main-text-color-1);
    padding: 5px;
    border: 0;
    font-size: var(--font-size-small);
    border-radius: var(--border-radius-2);
    outline: none;
}

input:focus {
    border: 1px solid var(--primary-color-1);
}

.sys-btn {
    width: 60px;
    height: 30px;
    line-height: 30px;
    background: var(--primary-color-1);
    text-align: center;
    border-radius: var(--border-radius-1);
}

.sys-icon-btn {
    width: 60px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: var(--font-size-big);
    color: var(--primary-color-1);
}

.sys-icon-btn.danger {
    color: red;
}

.sys-icon-btn.ignore {
    color: var(--main-text-color-2);
}