.add-todo-btn {
    color: var(--primary-color-1);
    font-size: var(--font-size-small);
    font-weight: bold;
}

.todo-line-main {
    display: flex;
    align-items: center;
    gap: 10px;
    overflow-x: auto;
}

.todo-line-item {
    flex-shrink: 0;
}

.todo-line-head {
    height: 30px;
    width: 100%;
    display: flex;
    margin-bottom: 5px;
    align-items: center;
}

.todo-line-icon {
    font-weight: bold;
    font-size: var(--font-size-mid);
    color: var(--no-active-color);
    margin-right: 10px;
}

.todo-line-icon-active {
    font-weight: bold;
    font-size: var(--font-size-mid);
    color: var(--primary-color-1);
    margin-right: 10px;
}

.todo-line {
    width: 50px;
    height: 2px;
    background: var(--no-active-color);
}

.todo-line-active {
    width: 50px;
    height: 2px;
    background: var(--primary-color-1);
}

.todo-line-time {
    height: 20px;
    line-height: 20px;
    font-size: var(--font-size-mini);
    color: var(--main-text-color-2);
}

.todo-line-title {
    font-size: var(--font-size-mid);
    font-weight: bold;
}