.system-env {
    width: calc(100% - 20px);
    padding: 11px 10px;
    display: flex;
    color: white;
}

.system-env-left {
    height: 100%;
    width: 50%;
    display: flex;
}
.env-date {
    font-size: var(--font-size-mid);
}
.env-hello {
    font-size: var(--font-size-small);
    color: white;
    font-weight: bold;
}

.system-env-right {
    height: 100%;
    width: 50%;
    display: flex;
    justify-content: right;
}

.env-date-info {
    height: 100%;
    display: flex;
    flex-direction: column;
    font-size: var(--font-size-small);
}

.date-info-yearmonth {
    height: 25px;
    line-height: 25px;
    width: 100%;
    color: var(--main-text-color-2);
    text-align: right;
}

.date-info-dateday {
    height: calc(100% - 25px);
    width: 100%;
    display: flex;
    flex-direction: row;
    font-size: var(--font-size-small);
}

.date-info-date {
    width: calc(100% - 50px - 5px);
    font-weight: bold;
    text-align: right;
    margin-right: 5px;
}

.date-info-day {
    width: 50px;
    font-weight: bold;
    text-align: right;
    color: var(--primary-color-2);
}