.habit-head-stat {
    height: 100%;
    font-size: var(--font-size-mini);
    color: var(--main-text-color-2);
    display: flex;
    align-items: center;
}
.add-habit-btn {
    text-align: center;
    margin-right: 10px;
    font-size: var(--font-size-small);
    color: var(--primary-color-1);
}
.habit-data-app-main {
    display: flex;
    align-items: center;
    gap: 20px;
    overflow-x: auto;
}

.habit-item {
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.habit-item-icon {
    font-size: var(--font-size-big);
}

.habit-item-time {
    font-size: var(--font-size-mini);
    color: var(--main-text-color-2);
}

.habit-item-title {
    font-size: var(--font-size-mid);
    font-weight: bold;
}

.habit-item-stat {
}


.done-list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.done-list-item {
    height: 60px;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.done-list-item-title {
    height: 100%;
    width: calc(100% - 60px);
    color: var(--main-text-color-1);
    font-size: var(--font-size-mid);
    display: flex;
    align-items: center;
}

.done-list-item-operate {
    width: 60px;
    height: 100%;
}

.done-list-btn {
    font-size: var(--font-size-small);
    font-weight: bold;
    color: var(--primary-color-1);
    cursor: pointer;
}