.xdata {
    position: absolute;
    left: 0;
    right: 0;
    top: 44px;
    bottom: 0;
    display: flex;
    flex-direction: column;
    padding-bottom: 34px;
    overflow: auto;
}

.xdata-type {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.xdata-type-head {
    width: calc(100% - 20px - 10px);
    position: sticky;
    top: 0;
    z-index: 100;
    padding: 11px 10px;
    padding-left: 20px;
}

.type-head-title:before {
    position: absolute;
    left: 10px;
    content: '';
    width: 5px;
    height: 26px;
    background-color: var(--primary-color-1);
}

.type-head-title {
    height: 100%;
    width: 100%;
    font-size: var(--font-size-mid);
    font-weight: bold;
    color: white;
}

.xdata-type-content {
    width: calc(100% - 20px);
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    gap: 16px;
}