:root {
    --main-bg-color-1: rgb(242, 242, 247);
    --main-bg-glass-color-1: rgb(242, 242, 247, 0.5);
    --main-bg-color-2: rgb(255, 255, 255);
    --main-bg-glass-color-2: rgb(255, 255, 255, 0.5);
    --main-text-color-1: black;
    --main-text-color-2: rgba(0, 0, 0, 0.5);
    --no-active-color: rgba(0, 0, 0, 0.1);
    --main-bg-img: linear-gradient(#0659a3, #87CEEB, #FFFFFF);
    --main-bg-img: url('../img/light.png');
    /*common*/
    --primary-color-1: #007aff;
    --primary-color-2: #f95b1a;
    --primary-color-3: #fec600;
    --font-size-mini: 14px;
    --font-size-small: 18px;
    --font-size-small-1: 20px;
    --font-size-mid: 22px;
    --font-size-big: 32px;
    --border-radius-1: 28px;
    --border-radius-2: 10px;
    --low-index: 0;
    --mid-index: 50;
    --hight-index: 80;
    --max-index: 100;
    --main-glass-filter: saturate(180%) blur(20px) !important;
}

:root[theme='dark'] {
    --main-bg-color-1: rgb(0, 0, 0);
    --main-bg-glass-color-1: rgb(0, 0, 0, 0.5);
    --main-bg-color-2: rgb(29, 29, 31);
    --main-bg-glass-color-2: rgb(29, 29, 31, 0.5);
    --main-text-color-1: white;
    --main-text-color-2: rgba(255, 255, 255, 0.5);
    --no-active-color: rgba(255, 255, 255, 0.1);
    --main-bg-img: linear-gradient(to bottom, #050505, #202020);
    --main-bg-img: url('../img/dark.png');
}

body {
    /* position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0; */
    height: 100vh;
    background: var(--main-bg-img);
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--main-text-color-1) !important;
}